import React, { useEffect } from "react";
import Input from "../../Components/Forms/Input/Input";
import Textarea from "../../Components/Forms/Textarea/Textarea";
import Button from "../../Components/Forms/Button/Button";
import "./addEvent.css";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";
import store, { addEvent } from "../../store";
import ImageUpload from "../../Components/Forms/ImageUpload/ImageUpload";
import OneImageUpload from "../../Components/Forms/OneImageUpload/OneImageUpload";
import Select from "../../Components/Forms/Select/Select";

import {
  // cleanForm,
  fetchCities,
  fetchCountries,
  fetchStates,
  cleanForm,
  formOnChangeArray,
  editAgendas,
} from "../../store";
import AddAgendasAddress from "./components/AddAgendasAddress";
import AgendaAdd from "./components/AgendaAdd";
import { scrollToView } from "../../helpers/scrollToView";

function AddEvent({
  addEvent,
  fetchCities,
  fetchCountries,
  fetchStates,
  countries,
  cities,
  countryId,
  stateId,
  states,
  agendas,
  cleanForm,
  formOnChangeArray,
  editAgendas,
}) {
  const history = useHistory();

  useEffect(() => {
    cleanForm();
    editAgendas([]);
    fetchCountries();
    formOnChangeArray("agendasAddresses", "agendas", []);
  }, []);

  useEffect(() => {
    if (countryId) {
      fetchStates(countryId);
    }
  }, [countryId]);
  useEffect(() => {
    if (stateId) {
      fetchCities(stateId);
    }
  }, [stateId]);

  const handleSubmit = (e) => {
    e.preventDefault();

    let {
      locationArm,
      locationEng,
      locationRu,
      cityId,
      nameArm,
      nameEng,
      nameRu,
      descriptionArm,
      descriptionEng,
      descriptionRu,
      startDate,
      endDate,
    } = store.getState().formReducer;

    // agendas, header, images
    // let { addresses } = store.getState().eventReducer;
    let { header, image } = store.getState().imageReducer;

    let event = {
      locationArm,
      locationEng,
      locationRu,
      cityId,
      nameArm,
      nameEng,
      nameRu,
      descriptionArm,
      descriptionEng,
      descriptionRu,
      startDate,
      endDate,
      agendas,
      header: header[0],
      images: image,
    };
    //
    const changePath = () => {
      history.push("/events");
    };
    addEvent(event, changePath);
    cleanForm();
    editAgendas([]);
  };

  const handleCancel = () => {
    history.push("/events");
    cleanForm();
    editAgendas([]);
  };

  return (
    <div>
      <div>
        <button onClick={() => history.goBack()} className="arrow_left">
          <i className="fas fa-chevron-left"></i>
        </button>
        <div className="add_member_title">
          <p>Add Event</p>
        </div>
      </div>
      <form
        onFocus={scrollToView}
        onSubmit={handleSubmit}
        className="add_event_container"
      >
        <div className="add_event_component">
          <div className="container_body">
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Input id="nameEng" type="text" placeholder="Name" />
              <Input id="nameArm" type="text" placeholder="Անվանում" />
              <Input id="nameRu" type="text" placeholder="Имя" />
            </div>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Select placeholder="Country" items={countries} id="countryId" />
              <Select placeholder="State" items={states} id="stateId" />
              <Select placeholder="City" items={cities} id="cityId" />
            </div>

            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Input
                id="startDate"
                type="datetime-local"
                placeholder="Start Date"
              />
              <Input
                id="endDate"
                type="datetime-local"
                placeholder="End Date"
              />
            </div>

            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Input id="locationEng" type="text" placeholder="Address" />
              <Input id="locationArm" type="text" placeholder="Հասցե" />
              <Input id="locationRu" type="text" placeholder="Адрес" />
            </div>

            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <Textarea
                id="descriptionEng"
                type="text"
                placeholder="Description"
              />
              <Textarea
                id="descriptionArm"
                type="text"
                placeholder="Նկարագիր"
              />
              <Textarea id="descriptionRu" type="text" placeholder="Описание" />
            </div>
            <div>
              {/* <AddAgendasAddress addressType={addressType} /> */}
              <AgendaAdd />
            </div>
            <div className="event_address_container " style={{ marginTop: 20 }}>
              <div style={{ marginRight: 20 }}>
                <OneImageUpload label="Upload Header Image" />
              </div>
              <ImageUpload label="Upload Images" />
            </div>
          </div>

          <div className="event_action_container">
            <Button
              onClick={handleCancel}
              title="Cancel"
              className="action_btn cancel_btn"
            />
            <Button title="Create" className="action_btn" />
          </div>
        </div>
      </form>
    </div>
  );
}

const mapStateToProps = (state) => {
  // console.log(state, "state|||");
  return {
    countries: state.locationsReducer.countries,
    countryId: state.formReducer?.countryId,
    states: state.locationsReducer.states,
    stateId: state.formReducer?.stateId,
    cities: state.locationsReducer.cities,
    cities: state.locationsReducer.cities,
    agendas: state.eventReducer?.agendas,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addEvent: (event, path) => dispatch(addEvent(event, path)),
    fetchCountries: () => dispatch(fetchCountries()),
    fetchStates: (country) => dispatch(fetchStates(country)),
    fetchCities: (state) => dispatch(fetchCities(state)),
    cleanForm: () => dispatch(cleanForm()),
    formOnChangeArray: (firstKey, secondKey, value) =>
      dispatch(formOnChangeArray(firstKey, secondKey, value)),
    editAgendas: (agendas) => dispatch(editAgendas(agendas)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(AddEvent);
